      description:
        "If you are looking for help on Similar triangles and its properties, you are exactly at the place you need to be.\n\nIn this video, I will be teaching you guys on how to find if triangles are similar using similar triangles properties. Now, lets understand what do we mean by a scale diagram.\n\nA diagram that is an enlargement or reduction of another diagram is called a scale diagram. When the Scale diagram is smaller than the original diagram, we call it a reduction and in a reduction, the scale factor will be less than 1. The scale factor is the relationship between the matching lengths of the two diagrams.\n\nWhat is a \u2018Triangle\u2019?\nA triangle is a closed, two-dimensional figure that has three straight sides. The point where two straight lines in a triangle meet is known as the vertex. Each vertex makes an angle. So a triangle has three vertices, three angles, and three edges. Of all the three sides, the base of the triangle is any one side. The length of the line drawn perpendicular to its base that joins the vertex on the opposite side of the base is called the height or altitude of the triangle. The figure is illustrated below.\n\n1) Isosceles Triangle : Two sides of the triangle will be equal\n2) Scalene Triangle : All of the sides of the triangle will be different\n3) Equilateral Triangle : All of the sides of the triangle will be equal\n4) Acute-angled triangle : In an acute-angled triangle, all of the 3 angles in the  triangle will be acute angles, which means that all of the angles will be less than 90 degrees.\n5) Obtuse-angled triangle : In an obtuse-angled triangle, 1 of the angles will be obtuse (which means more than 90 degrees and less than 180 degrees) and the remaining 2 of the angles will be acute angles (less than 90 degrees)\n6) Right-angled triangle : In a right-angled triangle, 1 of the angles will be a right angle (exactly 90 degrees) and the remaining 2 angles will be acute angles (less than 90 degrees)\n\nTo find the Scale Factor of a scale diagram, we divide the length on the scale diagram with the length on the original diagram.\n\nWhy is this important?\n- Construction workers when they construct building and bridges\n- motorists when they use maps to get around a city\n\nI hope this video helped!  If it did, be sure to SUBSCRIBE for more content.  Thanks for watching!\n\nTo understand more about how to find scale factors with similar figures please watch: https://youtu.be/GpoeNPJqQMU\n\nWatch Math tutorials with Maths with Mr.Sa.\n\nAbout Math with Mr.Sa (Teacher Satya): This channel offers instructional videos that go step-by-step thoroughly on various topics for Grades 5 \u2013 10. The videos can be used to introduce content, reteach content, or as an extra study guide/tool to enhance understanding. Teachers, parents/guardians, and students all over the world have used this channel to help with math content in many different ways. The Math Videos from this channel will cover topics that follow the IGCSE, GED, CBSE, THAI, and MANITOBA curriculums.\n\nFollow me on Facebook: https://web.facebook.com/TeacherSatya \nFollow me on Twitter: @TeacherSatya \nVisit www.mathswithmrsa.com for more free math videos and extra based content.\n\n#similartriangles #triangles #scalediagram",
